.header_main {
  height: 70px;
  line-height: 60px;
  border-bottom: solid gray thin;
  position: relative;
  padding: 5px;
  box-sizing: border-box;
  vertical-align: middle;
}
.header_main .username {
  font-size: large;
  margin-left: 20px;
}

.header_main .logout {
  display: flex;
  align-items: center;
  line-height: 70px;
  position: absolute;
  right: 3%;
  top: 50%;
  transform: translateY(-50%);
}

.header_main .logout span {
  margin-right: 5px;
}

.main {
  padding: 10px 5px;
}
